
.footer{
    background-color: #fff;
    padding: 50px 0px;

    &__wrapper{
        > div {
            flex-wrap: w;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }
    &__item{
        margin-right: 20px;
        display: flex;
        flex-direction: column;
        align-items: start;
        h3{
            color: #275F8C;
            font-size: 20px;
            margin-bottom: 27px;
            text-transform: uppercase;
            font-weight: 600;
        }
        > a{
            color:#BBBBBB;
            margin-bottom: 20px;
            display: inline-block;
            text-transform: uppercase;
        }
    }
    a{
        &:hover{
            text-decoration:underline;
        }
    }
    .socials{
        max-width: 149px;
        display: flex;
        flex-wrap: wrap;
        li{
            padding: 14px;
            width: 50%;
            a:hover{
             transform: translateY(-5px);
            }
        }

    }
    &__footer{
        margin-top: 40px;
        align-items: center;
        img{
            margin-bottom: 14px;
        }
        p{
            color: #BCBCBC;
            font-size: 16px;
            font-weight: 700;
            svg{
                margin-right: 10px;
            }
        }
        a{
            font-weight: 700;
            margin-top: 14px;   
            color:#585858;
            font-size: 16px;
        }
    }
}

.mail-btn{
    background: linear-gradient(112.19deg, #BDD4DC 4.95%, #C7DEDB 55.63%, #EBE4D4 87.94%);
    color: #fff !important;
    font-weight: 500;
    padding: 19px 17px;
    border-radius: 10px;
}


@media (max-width: 600px) {
    .footer{
        h3{
            font-size: 24px;
        }
        a{
            font-size: 21px;

        }
        &__footer{
            flex-direction: column;
            align-items: start;
            >div{
                margin-bottom: 20px;
                &:last-child{
                    display: flex;
                    justify-content: space-between;
                }
            }
            p{
                font-size: 16px;
            }
        }
        &__item{
            margin-bottom: 25px;
        }
        &__item_soc{
            width: 100%;
            .socials{
                max-width: 450px;
                li{
                    width: 21%;

                }
            }
        }
    }
}