

.project-card{
    box-shadow: 0px 12px 49px #B9D2D2;
    border-radius: 20px;
    position: relative;
    margin-bottom: 50px;
    padding: 50px 30px;
    &:last-child{
        margin-bottom: 0;
    }
    &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 20px;
        background: linear-gradient(109.9deg, #BDD4DC 5.36%, #C7DEDB 62.32%, #EBE4D4 98.63%);
        left:0px;
        top: 0px;
        z-index: 0;
    }
    &__wrapper{
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: space-between;
    }
    &__left{
        margin: 0px 10px;
    }
    &__img{
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 30px;
        border-radius: 20px;
        width: 364px;
        height: 287px;

    }

    &__text{
        max-width: 680px;
        h3{
            color:#657593;
            font-size: 40px;
            margin-bottom: 20px;
            font-weight: 500;
        }
        p{
            color: #657593;
            font-size: 32px;
        }
    }
    .ellipse{
        position: absolute;
        z-index: -1;
        left: -29px;
        width: 40%;
        top: 50%;
        transform: translateY(-50%) scale(1);
    }
}

@media (max-width: 900px) {
    .project-card{
        &__wrapper{
            flex-direction: column;

        }

        &__left{
            order: 0 !important;
            margin-bottom: 45px;
        }
        .ellipse{
            width: 100%;
            left: 38% !important;
            top:45% !important;
            transform: translate(-50%, -50%) !important;
        }
        &__img{
            margin: 0 auto;
        }
        &__text{
            text-align: center;
            max-width: 700px;
            margin: 0 auto;
        }
    }
}


@media (max-width: 500px) {
    .project-card__img{
        width: 100%;
        height: 250px;
        padding: 50px;
        img{
            // width: 100%;
        }
    }
}