
.burger-menu{
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: #fff;
    transition-duration: 1s;
    top:-150%;
    z-index: 50;
    &__wrapper{
        padding: 80px 20px 10px 20px;
        overflow: auto;

    }
    a{
        color: #000;
        font-size: 25px;
        margin-bottom: 20px;
        display: block;
        font-weight: 600;
        text-transform: uppercase;

        &::after{
            content: '';
            display: block;
            width: 100%;
            height: 2px;
            background-color: #fff;
            margin-top: 3px;
            transform: scale(0, 1);
            transform-origin: left;
            transition-duration: 0.3s;
        }
        &:hover{
            &:after {

                // transform-origin: ;
                transform: scale(1, 1);
            }
            font-weight: 700;
        }
    }
    &.active{
        top: 0px;
    }
}