

.home{
    &__wrapper{
        padding: 80px 20px;
        
    }
    &__body{
        position: relative;
        width: 100%;
        height: 100%;
        min-height: calc(100vh - 80px*2);
        img, p{
            top: 50%;
            left: 0px;
            transform: translateY(-50%);
            width: 100%;
            position: absolute;
        }
        img{
            object-fit: contain;
            height: 100%;
        }
        p{
            font-size: 230px;
            color: #FFFFFF;
            text-shadow: 0px 0px 3px #000;
            text-align: center;
        }
    }
}