

.card {
    width: 100%;
    background-color: #fff;
    padding: 50px 70px;
    border-radius: 20px;
    

    &__img{
        border-radius: 20px;
        margin-bottom: 22px;
        overflow: hidden;
        img{
            width: 100%;
        }
    }

    &__text{
        margin-bottom: 22px;
        padding: 0px 20px;
    }
    h3{
        font-weight: 600;
        font-size: 32px;
        color: #657593;
        margin-bottom: 25px;
    }
    p{
        color: #C8C8C8;
    }
    &__footer{
        display: flex;
        justify-content:flex-end;
    }
    &__date{
        font-size: 24px;
        color: #C5CFD4;
        font-weight: 700;
    }
}


@media (max-width:900px) {
    .card{
        padding: 35px 40px;
    }
}