

.about{
    &__wrapper{
        display: flex;
        justify-content: space-between;
    }
    &__left{
        margin-right: 20px;    
        flex: 0 1 400px;
    }
    &__right{
        flex: 0 1 533px
    }
    &__img{
        position: relative;
        width: 100%;
        height: 100%;
        svg{
            width: 100%;
            height: 100%;
        }
        .logo{
            position: absolute;
            top: 58%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    
}

@media (max-width: 900px) {
    .about{

        &__left{
            flex: 0 0 0px;
            position: relative;
        }
        &__right{
            flex: 1 1 auto
        }
        &__img{
            position: absolute;
            width: 300px;
            height: 300px;
            top: -154px;
            left: 249px;
        }
    }
}
@media (max-width: 600px) {
    .about__img{
        width: 150px;
        height: 150px;
        top: -58px;
        left: 129px;
    }
}