
.burger-icon{
    width: 28px;
    height: 17px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before,&::after{
        content: '';
        width: 100%;
        height: 2px;
        background-color: #fff;
        position: absolute;
        left: 0px;
        transform-origin: center;
        transition-duration: 0.3s;
    }   
    span{
        display: block;
        width: 100%;
        height: 2px;
        background-color: #fff;
        transition-duration: 0.3s;
    }
    &::before{
        top: 0px;
    }
    &::after{
        bottom: 0px;
    }

    &.active{
        &::before{
            transform: rotate(45deg);
        }
        &::after{
            transform: rotate(-45deg);
        }

        span{
            opacity: 0;
        }
    }
}