

.contacts{
    text-align: center;
    h2{
        // font-family: 'Syncopate';
        font-size: 64px;
        &::after{
            display: none;
        }
    }

    &__body{
        max-width: 800px;
        margin: 30px auto;
        
        margin-top: 50px;
        &::before,&::after{
            content: '';
            display: block;
            width: 90%;
            height: 2px;
            background-color: #fff;
        }

    }

    .socials {
        margin: 90px 0px;

        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
            width: 21%;
            padding: 4%;
        }
    }
}

@media (max-width: 600px) {
    .contacts {
        h2{
            font-size: 32px;
        }
        &__body{
            margin-top: 0px;
        }
        .socials {
            padding: 38px 0px;
            margin: 0px;
        } 
    }
}