.header{
    width: 100%;
    position: absolute;
    left: 0px;
    height: 0px;
    // padding: 30px 0px;
    z-index: 70;
    height: 70px;
    
    &__wrapper{

    }
    nav{
        height: 100%;
        align-items: center;
        display: flex;
        justify-content: space-between;

        a{
            margin-right: 10px;
            color: #fff;
            font-size: 24px;
            transition-duration: 0.3s;
            text-transform: uppercase;
            display: block;
            &::after{
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                background-color: #fff;
                margin-top: 3px;
                transform: scale(0, 1);
                transform-origin: left;
                transition-duration: 0.3s;
            }
            &:hover{
                &:after {

                    // transform-origin: ;
                    transform: scale(1, 1);
                }
                font-weight: 700;
            }
        }
    }
    &__mob{
        display: none;
    }
    &.fixed{
        color: #000 !important;
        background-color: #fff;
        transition-duration: 0.3s;
        box-shadow: 0px 0px 10px #000;
        position: fixed;
        a{
            color:#000 !important;
        }
        .burger-icon {
            &:before,&:after,span{
                background-color: #000;
            }
            &::before{
                top:5px;

            }
            &::after{
                bottom:10px;

            }

        }
    }
}

@media (max-width: 900px) {
    .header{
        // background-color: #fff;
        nav{
            display: none;
        }
        padding: 0px 0px;
        
        height: 50px;
    }

    .header__mob{
        display: flex;
        width: 100%;
        height: 100%;
        padding: 5px 20px;
        justify-content: space-between;
        align-items: center;

        .logo{
            height: 100%;
        }
    }
}