.app {
  background: linear-gradient(178.93deg, #89C6F8 0.92%, rgba(82, 129, 167, 0) 155.82%);
  overflow-anchor: hidden;
  position: relative;
}
.wrapper{
  position: relative;
  /* overflow-x: hidden; */
}
section{
  width: 100%;
  margin-bottom: 100px;
}
.container{
  max-width: 1180px;
  margin: 0 auto;
  padding: 0px 20px;
}


p{
  font-size: 32px;
  color: #fff;
  line-height: 1.5;
}

h2{
  font-size: 64px;
  margin-bottom: 50px;
  color: #fff;
  display: inline-block;
}

h2:after{
  content: '';
  width: 100%;
  display: block;
  height: 1px;
  background-color: #fff;
  margin-top: 25px;

}

.container-bg{
  position: relative;
}
.bg{
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: 0;
  position: absolute;
}

@media (max-width: 600px) {
  h2{
    font-size: 32px;
  }
  p{
    font-size: 20px;
  }
}