
.projects{
    .container{
        text-align: center;
    }
    h2{
        margin-bottom: 50px;
    }

    .project-card{
        margin-bottom: 80px;
        max-width: 1283px;
        
    }
    .project-card:nth-child(2n) {
        margin-left: auto;
        &::before{
            transform: scale(-1);
        }
        .project-card__left{
            order: 2;
        }
        .ellipse {
            left: auto;
            right: -29px;
            transform: translateY(-50%) scale(-1);
        }
    }
    &__footer{
        display: flex;
        justify-content:end;
    }
    .more{
        font-size: 32px;
        padding: 32px 60px;
        flex:0 1 604px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #fff;
        border: solid 1px #fff;
        border-radius: 20px;
        &:after{
            content: '';
            flex: 0 1 114px;
            height: 2px;
            background-color: #fff;
            transition-duration: 0.4s;
            transform-origin: center;
        }

        &:hover{
            &:after{
                transform: scale(0, 1);
            }
        }
    }
}

@media (max-width: 900px) {
    .projects{
        padding: 0px 20px;
    }

    
}
@media (max-width: 600px) {
    .project-card{
        p{
            font-size: 20px;
        }
        h3{
            font-size: 30px;
        }
    }
    .more{
        font-size: 25px;
    }
}